.sitemap {
	margin-bottom: 30px;
	padding-top: 60px;
	padding-bottom: 60px;
}

.sitemap-heading {
    font-size: 1.2857142857rem;
    color: #0f848d;
    text-transform: uppercase;
    margin-bottom: 1.5rem;
    font-weight: 600;
    line-height: 30px;
}

/* ------------------------------------------------------------
	Page Styles
------------------------------------------------------------ */

/* -------- Top Level --------- */

.AspNet-TreeView {
	clear: both;
	width: 100%;
	margin-top: 3em;
}
.AspNet-TreeView #home {
	position: absolute;
	margin-top: -3em;
	margin-bottom: 0;
	min-width: 11.5em;
	width: 100%;
}
.AspNet-TreeView #home:before {
	display: none;
}
.AspNet-TreeView ul {
	display: flex;
	flex-wrap: wrap;
	list-style: none;
	position: relative;
}
.AspNet-TreeView li {
	flex: 1;
	flex-basis: 11.5em;
	padding-right: 1.25em;
	position: relative;
}
.AspNet-TreeView > ul > li {
    margin-top: 3em;
}
.AspNet-TreeView li a {
    margin: 0;
	display: block;
    line-height: 22px;
    padding: 15px;
    padding-right: 30px;
	font-size: .9375em;
	font-weight: bold;
	color: white;
	background: #0090ff;
	text-shadow: 0 0 10px rgba(0,0,0,.1);
	border: 1px solid  rgba(0,0,0,.025);
	box-shadow: 0px 2px 1px rgba(0,0,0,0.15);
	text-decoration: none;
}
.AspNet-TreeView li a:hover {
	background-color: #0070f9;
}
/* -------- Second Level --------- */

.AspNet-TreeView ul ul {
	display: block;
}
.AspNet-TreeView ul ul li {
	margin-top: .6875em;
	padding-right: 0;
}
.AspNet-TreeView ul ul li a {
	background-color: #28bf5e;
}
.AspNet-TreeView ul ul li a:hover {
	background-color: #09a63e;
}
.AspNet-TreeView ul ul li:first-child {
    padding-top: 0em !important;
    margin-top: 15px;
}

/* -------- Third Level --------- */

.AspNet-TreeView ul ul ul {
	margin-top: 0;
	padding-top: .6em;
	padding-bottom: 0;
}
.AspNet-TreeView ul ul ul li {
	padding-top: 0;
	padding-bottom: 0;
}
.AspNet-TreeView ul ul ul li a {
	background-color: #ff9d00;
	font-size: .75em;
	padding: .75em;
	width: 90%;
	margin-right: 0;
	margin-left: auto;
}
.AspNet-TreeView ul ul ul li a:hover {
	background-color: #ff831b;
}
.AspNet-TreeView ul ul ul li:first-child {
	padding-top: 1em;
}
.AspNet-TreeView ul ul ul li a:link:after,
.AspNet-TreeView ul ul ul li a:visited:after {
	font-size: .75em;
}

/* --------	Fourth Level --------- */

.AspNet-TreeView ul ul ul ul {
	margin-top: 0;
	padding-top: .3125em;
	padding-bottom: .3125em;
}
.AspNet-TreeView ul ul ul ul li a {
	background-color: #de003a;
	padding: .75em ;
	width: 80%;
}
.AspNet-TreeView ul ul ul ul li a:hover {
	background-color: #c20035;
}
.AspNet-TreeView ul ul ul ul li a:link:after,
.AspNet-TreeView ul ul ul ul li a:visited:after {
	display: none;
}

.AspNet-TreeView-Collapse, .AspNet-TreeView-Expand {
    position: absolute;
    top: 0;
    right: 20px !important;
    background-color: rgba(0,0,0,.5) !important;
    width: 30px !important;
    height: 30px !important;
    z-index: 1;
    padding: 0 !important;
    border: 0 !important;
    box-shadow: none !important;
    outline: none !important;
    display: flex !important;
    justify-content: center;
    align-items: center;
    font-size: 0 !important;
}

.AspNet-TreeView-Collapse:before {
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: "Material Design Icons";
    font-size: 18px !important;
    font-weight: 900 !important;
    color: #fff !important;
    content: "\f143";
}

.AspNet-TreeView-Expand:before {
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: "Material Design Icons";
    font-size: 18px !important;
    font-weight: 900 !important;
    color: #fff !important;
    content: "\f140";
}

.AspNet-TreeView-Parent .AspNet-TreeView-Expand,
.AspNet-TreeView-Parent .AspNet-TreeView-Collapse {
    right: 0 !important;
}

.AspNet-TreeView-Show {
    display: block !important;
}

.AspNet-TreeView-Hide {
    display: none !important;
}

.AspNet-TreeView ul:before,
.AspNet-TreeView ul:after,
.AspNet-TreeView ul li:before,
.AspNet-TreeView ul li:after {
	display: block;
	content: '';
	position: absolute;
	border-width: 0;
	border-color: #e3e3e3;
	border-style: solid;
	z-index: -1;
	/* border-color: red; */
}
.AspNet-TreeView > ul > li:before {
	height: 1.375em;
	top: -1.375em;;
	right: calc(50% + .625em);
	width: calc(100% - 2px);
	border-top-width: 2px;
	border-right-width: 2px;
	/* border-color: red; */
}
.AspNet-TreeView > ul > li:first-child + li:before {
	border-top-width: 0;
	height: 5em;
	top: -5em;
	/* border-color: red; */
}
.AspNet-TreeView ul ul li:after {
	width: 50%;
	height: .6875em;
	top: 0;
	right: 1px;
	border-left-width: 2px;
	/* border-color: red */;
}
.AspNet-TreeView ul ul li:first-child:before {
	width: 50%;
	height: 1.3125em;
	top: .6875em;
	right: 1px;
	border-left-width: 2px;
	/* border-color: red; */
}
.AspNet-TreeView > ul > li:last-child:after {
	border-bottom-width: 0;
}
.AspNet-TreeView ul ul ul li:before {
	width: calc(50% - 5px) !important;
	height: calc(100% - 2px);
	top: -50%;
	left: 0;
	border-left-width: 2px;
	border-bottom-width: 2px;
	/* border-color: red; */
}
.AspNet-TreeView ul ul ul li:first-child:before {
	height: 2.125em;
	top: -1px;
	border-top-width: 2px;
	/* border-color: red; */
}
.AspNet-TreeView ul ul ul:before {
	width: 50%;
	height: 1.25em;
	top: -10px;
	right: 1px;
	border-left-width: 2px;
	/* border-color: red; */
}
.AspNet-TreeView ul ul ul li:after {
	border-width: 0;
}
.AspNet-TreeView ul ul ul ul li:before,
.AspNet-TreeView ul ul ul ul li:first-child:before {
	display: none;
}

.AspNet-TreeView ul ul ul ul:before {
	width: 1px;
	height: calc(100% + 2.5em);
	top: -2.5em;
	left: 0;
	border-left-width: 2px;
	/* border-color: red; */
}

/* ------------------------------------------------------------
	Utility Navigation
------------------------------------------------------------ */

.utilityNav {
	float: right;
	margin-top: 0;
	margin-bottom: -.25em;
	margin-right: 1.25em;
	max-width: 48%;
	list-style-type: none;
}
.utilityNav li {
	padding: 0 0 .625em .625em;
	display: inline-block;
}
.utilityNav li:first-child {
}
.utilityNav li a {
	display: block;
	font-size: .75em;
	font-weight: 700;
	padding: .75em 1em;
	font-weight: bold;
	text-align: left;
	color: white;
	background-color: #ff9d00;
	border: 1px solid  rgba(0,0,0,.025);
	box-shadow: 0px 2px 0 rgba(0,0,0,0.15);
	text-shadow: 0 0 10px rgba(0,0,0,.15);
	text-decoration: none;
}
.utilityNav li a:hover {
	background-color: #ff831b;
}
.utilityNav li a:link:after,
.utilityNav li a:visited:after {
	color: rgba(255,255,255,.75);
	font-size: .75em;
	font-weight: 600;
	margin-top: .25em;
}

/* ------------------------------------------------------------
	Responsive Styles
------------------------------------------------------------ */

@media screen and (max-width: 30em) {
	.AspNet-TreeView ul {
		display: block;
	}
	.AspNet-TreeView li {
		width: 100%;
		padding-right: 0;
	}
	.AspNet-TreeView #home {
		width: 100%;
		position: relative;
		margin-bottom: -1em;
		margin-top: 0;
	}
	.utilityNav {
		float: none;
		display: block;
		width: 100%;
		text-align: right;
		margin-bottom: 2.5em;
		max-width: 100%;
	}
}

/* The media queries below keep the grid column widths intact when
a column wraps. This prevents the wrapped column from displaying at
100% width. These queries also make the homepage line item flex
appropriately. When your sitemap is complete, remove the queries
that you don't need, and your sitemap layout will work perfectly. */

/* two column */
@media screen and (min-width: 30em) {
	.AspNet-TreeView > ul > li {	max-width: 50%;	}
}
/* three column */
@media screen and (min-width: 38.5em) {
	.AspNet-TreeView > ul > li {	max-width: 33%;	}
}
/* four column */
@media screen and (min-width: 50em) {
	.AspNet-TreeView > ul > li {	max-width: 25%;	}
}
/* five column */
@media screen and (min-width: 61em) {
	.AspNet-TreeView > ul > li {	max-width: 20%;	}
}
/* six column */
@media screen and (min-width: 73em) {
	.AspNet-TreeView > ul > li {	max-width: 16.66%;	}
}
/* seven column */
@media screen and (min-width: 84.5em) {
	.AspNet-TreeView > ul > li {	max-width: 14.285%;	}
}
/* eight column */
@media screen and (min-width: 96em) {
	.AspNet-TreeView > ul > li {	max-width: 12.5%; }
}
/* nine column */
@media screen and (min-width: 107.5em) {
	.AspNet-TreeView > ul > li {	max-width: 11.11%; }
}
/* ten column */
@media screen and (min-width: 119em) {
	.AspNet-TreeView > ul > li {	max-width: 10%; }
}